/* ---------------------------------------
   HEADER MAIN WRAPPER
---------------------------------------- */
#mainHeader {
  width: 100%;
  background: var(--bs-primary3);
  display: flex;
  align-items: center;
  padding: 4px 3px;
  position: sticky;
  top: 0;
  z-index: 999;
  border-bottom: 1px solid #222;
}

/* ---------------------------------------
   LOGO (Responsive)
---------------------------------------- */
#headerLogo,
.logo-img {
  height: 60px;
  width: auto;
  object-fit: contain;
  display: block;
  margin-right: 2px !important;
}

@media (min-width: 576px) {
  #headerLogo,
  .logo-img {
    height: 75px;
  }
}

@media (min-width: 768px) {
  #headerLogo,
  .logo-img {
    height: 90px;
  }
}

@media (min-width: 992px) {
  #headerLogo,
  .logo-img {
    height: 90px;
  }
}

@media (min-width: 1200px) {
  #headerLogo,
  .logo-img {
    height: 100px;
  }
}

/* ---------------------------------------
   HAMBURGER BUTTON
---------------------------------------- */
.sidebar-toggle-btn {
  cursor: pointer;
  padding: 3px;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#sidebarToggleBtn {
  margin-left: 0 !important;
  margin-right: 3px !important;
}

#sidebarToggleBtn svg {
  font-size: 26px;
}

/* Extra small fix */
@media (max-width: 576px) {
  #sidebarToggleBtn {
    margin-right: 4px !important;
  }
}

/* ---------------------------------------
   HEADER CONTAINER
---------------------------------------- */
#topHeader {
  width: 100%;
  display: flex;
  align-items: center;
  margin-left: 6px !important;
}

/* ---------------------------------------
   NAVBAR LIST (reduced gap)
---------------------------------------- */
#headerNavList {
  gap: 0 !important; 
  justify-content: flex-end !important;
}

#headerNavList li .nav-link {
  padding: 6px 10px !important; 
  font-weight: 600;
  cursor: pointer;
  color: var(--bs-primary2);
}

#headerNavList li .active-link {
  color: #ffc107;
  font-weight: bold;
}

/* ---------------------------------------
   HEADER RIGHT SECTION
---------------------------------------- */
#headerRight {
  display: flex;
  align-items: center;
  margin-left: auto;
  gap: 10px;
}

/* ---------------------------------------
   MOBILE USER DROPDOWN
---------------------------------------- */
#mobileUserDetails {
  position: relative;
}

#mobileUserOptions li a {
  display: flex;
  gap: 6px;
  padding: 5px 12px;
  color: var(--bs-primary2);
}

/* ---------------------------------------
   PROFILE IMAGE
---------------------------------------- */
#profileImage {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  object-fit: cover;
}

/* ---------------------------------------
   NOTIFICATION ICON
---------------------------------------- */
.circle.green {
  background: #28a745;
  color: #fff;
  padding: 2px 6px;
  border-radius: 50%;
  font-size: 11px;
}

/* ---------------------------------------
   MOBILE EXTRA FIXES
---------------------------------------- */
@media (max-width: 576px) {
  #mainHeader {
    padding: 3px 5px !important;
  }

  #headerLogo {
    height: 75px !important;
  }

  #topHeader {
    margin-left: 4px !important;
  }
}
